.form-cont {
    background: white;
    padding: 24px;
    border-radius: 8px;
    box-shadow: 2px 5px 10px rgba(black, 0.2);

    input {
        background: rgba(grey, 0.1);
        border: none;
        padding: 8px 16px;
        border-radius: 8px;
        outline: none;
    }

    button {
        border-radius: 8px;
        padding: 4px 8px;
        background: #f7921d;
        color: white;
        cursor: pointer;
        border: none;
        outline: none;
    }
}
