.book-cont {
    background: rgba(black, 0.04);
    height: fit-content;
    cursor: pointer;
    margin: 8px 16px;
    padding: 8px 16px;
    border-radius: 12px;

    h3{
        font-size: 32px;
        font-weight: 500;
    }

    button {
        border-radius: 12px;
        padding: 4px 8px;
        margin: 0 4px;
        background: #f7921d;
        color: white;
        cursor: pointer;
        border: none;
        outline: none;
    }
}
