.modal{
    display: none;
    background: rgba(black, 0.5);
    height: 100vh;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    &.open{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    span{
        position: absolute;
        font-size: 24px;
        color: white;
        right: 24px;
        top: 24px;
    }
    section.m-section{
        background: white;
        flex-direction: column;
padding: 24px;
border-radius: 16px;
    }

}